@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Newsreader:wght@400;700&family=Raleway:wght@400;500&display=swap);
/* CLASSES TO BE RE-USED THROUGHOUT */
* {
  box-sizing: border-box;
  outline: none;
}

.container {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

.highlight {
  font-weight: bold;
  color: #ff8800;
  transition: all 0.5s ease-in-out;
}

.large {
  font-size: 40px;
}

.content-inner {
  padding: 0 10px;
}

.button-solid {
  border: none;
  background-color: #ff8800;
  font-weight: bold;
  color: #fff;
  padding: 0.5rem 0.8rem;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.button-solid:hover {
  background: #ff8800d7;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

/* REMOVING SOME DEFAULT STYLING FROM HTML ELEMENTS */

h1 {
  margin: 0;
}

li {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

a {
  color: #ff8800;
  text-decoration: none;
}

ul {
  padding-left: 20px;
  list-style-type: square;
}

/* ANIMATIONS */
@keyframes FadeDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* BODY */
body {
  font-family: "Raleway", sans-serif;
  margin: 0;
  padding: 0;
}

/* FOR SCREEN SIZES 570PX AND BELOW */
@media screen and (max-width: 570px) {
  .container {
    width: calc(100% - 32px);
  }

  .large {
    font-size: 32px;
  }
}

/* WELCOME AREA */
#welcome {
  background: url(/static/media/welcome.f324cedc.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  color: #ffffff;
  height: 100vh;
  position: relative;
  z-index: 1;
}

#welcome::after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  z-index: -1;
}

#welcome .welcome-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  animation: FadeDown 400ms ease-in-out forwards;
  text-align: center;
  opacity: 0;
}

#welcome .buttons {
  margin-top: 2rem;
  margin-bottom: 0;
}

#welcome .buttons .button-solid {
  margin: 0 0.5rem;
  padding: 1rem 2rem;
  border-radius: 50px;
}

#welcome .buttons .button-solid:last-child {
  background: #fff;
  color: #ff8800;
}

#welcome .buttons .button-solid:last-child:hover {
  background: #ff8800;
  color: #fff;
}

@media (max-width: 570px) {
  #welcome .welcome-content {
    text-align: center;
  }
}

/* CATEGORIES */
#categories {
  background: #ffe3c3;
  padding: 80px 0;
}

#categories h1 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 3rem;
}

#categories img {
  width: 100%;
  height: 200px;
}

#categories .highlight:hover {
  text-decoration: underline;
}

#categories .grid-4 {
  grid-gap: 32px;
  gap: 32px;
  text-align: center;
}

#categories .grid-item {
  background-color: #fff;
  transition: all 0.5s ease-out;
}

#categories .grid-item:hover {
  transform: translateY(-20px);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
}

/* FOR SCREEN SIZES 1000PX AND BELOW */
@media screen and (max-width: 1000px) {
  #categories .grid-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 570px) {
  #categories .grid-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* FOR SCREEN SIZES 460PX AND BELOW */
@media screen and (max-width: 480px) {
  #categories img {
    height: 240px;
  }

  #categories .grid-4 {
    grid-template-columns: 1fr;
  }
}

/* FOR SCREEN SIZES 400PX AND BELOW */
@media screen and (max-height: 400px) {
  #categories .grid-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* ABOUT AREA */
#about {
  padding: 80px 0;
}

#about img {
  width: 100%;
}

#about .grid-2 {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 32px;
}

#about .about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 570px) {
  #about .grid-2 {
    grid-template-columns: 1fr;
  }
}

@keyframes upDown {
  0% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(-20px);
  }
}

#social-links {
  text-align: center;
  border-top: 1px solid #fff;
  width: 50%;
  margin-top: 2rem;
}

#social-links h2 {
  font-size: 2rem;
  margin: 2rem 0;
}

#social-links .grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

#social-links .social-links {
  padding-top: 2rem;
}

#social-links .social-link-icon {
  transition: all 500ms ease-in-out;
}

#social-links .social-link-icon:hover {
  color: #fff;
  transform: translateY(-18px);
  animation: upDown 1000ms ease-in-out 500ms infinite;
}

#footer #social-links {
  margin: 0;
  padding: 0;
  text-align: left;
}

#footer #social-links h2 {
  display: none;
}

@media (max-width: 570px) {
  #social-links {
    width: 100%;
    margin-top: 2.5rem;
  }

  #social-links h2 {
    font-size: 1.6rem;
    margin: 1rem 0;
  }

  #social-links .social-links {
    padding-top: 1rem;
  }

  #footer #social-links {
    text-align: center;
  }
}

/* CONTACT SECTION */
#contact {
  background: url(/static/media/contact-background.21158ac8.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  padding: 3rem 0;
  color: #ffffff;
  position: relative;
  z-index: 1;
}

#contact::after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  z-index: -1;
}

#contact .container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
}

#contact input {
  border: none;
  border-radius: none;
  width: 240px;
  padding: 0.5rem 0.8rem;
  transition: all 0.4s ease-in-out;
}

#contact input:focus {
  background: #000000b6;
  color: #ff8800;
  outline-color: #ff8800;
}

@media (max-width: 570px) {
  #about .grid-2 {
    grid-template-columns: 1fr;
  }

  #contact p {
    display: none;
  }

  #contact .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  #contact input {
    width: 100%;
  }

  #contact .button-solid {
    margin-top: 8px;
    width: 100%;
  }
}

/* CATEGORIES SECTION 2 */
#categories-2 {
  text-align: center;
  padding: 2rem 0;
}

#categories-2 .category {
  padding: 2rem 0;
}

#categories-2 img {
  width: 100%;
}

#categories-2 .grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 32px;
  gap: 32px;
}

#categories-2 .grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 32px;
  gap: 32px;
}

.donation-form {
  background: url(/static/media/donate.1212e610.jpg) no-repeat center/cover;
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 1;
}

.donation-form::after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  z-index: -1;
}

.donation-form .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  animation: FadeDown 800ms ease-in-out forwards;
  opacity: 0;
}

.donation-form h1 {
  color: #fff;
  font-size: 2.7rem;
  margin-bottom: 1.5rem;
}

.donation-form input {
  width: 500px;
  height: 2rem;
  margin: 0.9rem 0;
  padding: 1rem;
  border: none;
}

.donation-form input:focus {
  background: #000000b6;
  color: #ff8800;
  outline-color: #ff8800;
}

.donation-form .button-solid {
  font-weight: bold;
  font-size: 1rem;
  text-transform: uppercase;
  height: 2.7rem;
  text-align: center !important;
}

@media (max-width: 640px) {
  .donation-form h1 {
    font-size: 2rem;
  }

  .donation-form .button-solid {
    width: 100%;
  }

  .donation-form input {
    width: 265px;
  }
}

/* HEADER */
#header {
  width: 100%;
  background: #ff8800dc;
  position: fixed;
  top: 0;
  z-index: 1000;
}

#header .logo {
  font-family: "Dancing Script", "Newsreader", serif, cursive;
  font-size: 20px;
  font-weight: 900;
  color: #ffffff;
  padding: 0;
}

#header a {
  color: #000;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease-in;
  margin: 0;
  padding: 1rem;
}

#header a:hover {
  color: #ffffff;
}

#header .main-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0;
  align-items: center;
}

#header .nav-links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#header .nav-links a {
  font-size: 13px;
  margin: 0;
}

#header .nav-links a:hover {
  background: #333333dc;
}

#header .burger {
  display: none;
}

/* MEDIA QUERIES */

/* FOR SCREEN SIZES 990PX AND BELOW */
@media screen and (max-width: 990px) {
  #header .logo {
    line-height: 10vh;
    font-size: 1.8rem !important;
  }

  #header .nav-links {
    background-image: linear-gradient(to bottom, #000, #000000dc);
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    top: 10vh;
    left: 0;
    width: 100%;
    height: 50vh;
    transform: translateX(-220%);
    transition: all 1000ms cubic-bezier(1, -0.51, 0.03, 1.27);
    z-index: -1;
  }

  #header .nav-links.active {
    transform: translateX(0);
  }

  #header a {
    color: #fff;
  }

  /* BURGER MENU */
  #header .burger {
    display: inline-block;
    cursor: pointer;
  }

  #header .burger .burgerLine {
    width: 30px;
    height: 3px;
    margin: 5px 0;
    background: #fff;
    border-radius: 10px;
  }
}

@media (max-width: 570px) {
  #header li:first-child {
    display: none;
  }

  #header a {
    padding: 16px 8px;
  }

  #header .logo {
    font-size: 16px;
  }

  #header .main-nav {
    width: calc(100% - 32px);
  }
}

/* FOOTER */
#footer {
  background-color: #0c0c0c;
  color: #fff;
}

#footer .grid-2 {
  display: grid;
  grid-template-columns: 2fr 1fr;
  padding: 24px 0;
  grid-gap: 24px;
  gap: 24px;
}

#footer .links a {
  color: #ffffff;
  transition: all 400ms ease-in-out;
}

#footer .links a:hover {
  color: #ff8800;
}

#footer .copyright {
  background: #000;
  display: flex;
  justify-content: center;
}

#footer .copyright .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#footer .copyright .developer {
  color: #999;
  font-size: 13px;
}

#footer .copyright p {
  margin: 0;
  text-align: center;
  padding: 1rem 0;
}

@media (max-width: 570px) {
  #footer .grid-2 {
    grid-template-columns: 1fr;
  }

  #footer .copyright .container {
    flex-direction: column;
  }
}

@media screen and (max-height: 400px) {
  #footer .grid-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}

